footer {
  display: block;
  position: relative;
  padding: 2rem 3rem 0;
  background: #dad3dc;
  width: 100%;
  overflow: hidden;
  margin-bottom: 0;
}

footer section:nth-child(1) {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  padding-bottom: 0;
  justify-content: center;
}

footer section:nth-child(1) .join-us {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-left: 0;
  gap: 0.7rem;
}

footer .contact-icons-bx div span,
footer .app-icons-bx div span {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  margin-bottom: 0.2rem;
}

footer .join-us div a {
  text-decoration: none;
  color: #000;
  margin-right: 0.4rem;
}

footer .app-icons-bx div span,
footer .join-us div span {
  cursor: pointer;
}

footer .services-bx div span {
  display: block;
  margin-bottom: 0.2rem;
}

footer hr {
  margin: 0;
}

footer section:nth-child(2) {
  margin: 0;
  padding: 0;
}

footer #sct2-item {
  text-align: center;
  margin: 0;
  padding: 1rem 0;
}

@media only screen and (max-width: 400px) {
  footer section:nth-child(1) {
    flex-direction: column;
  }
  footer section:nth-child(1) div h4 {
    text-align: center;
  }
  footer section:nth-child(1) div div {
    margin-left: 4rem; 
  }
  .join-us div span {
    margin-right: 1.8rem;
  }
}
